export default "\nexport const addDotButtonsAndClickHandlers = (emblaApi, dotsNode, \n/*__NAV_AUTOPLAY_REPLACE_START__*/\nonButtonClick\n/*__NAV_AUTOPLAY_REPLACE_END__*/\n) => {\n    let dotNodes = [];\n    \n    const addDotBtnsWithClickHandlers = () => {\n        dotsNode.innerHTML = emblaApi\n            .snapList()\n            .map(() => '<button class=\"embla__dot\" type=\"button\"></button>')\n            .join('');\n        \n        const scrollTo = (index) => {\n            emblaApi.goTo(index);\n            /*__NAV_AUTOPLAY_REPLACE_START__*/\n            if (onButtonClick)\n                onButtonClick(emblaApi);\n            /*__NAV_AUTOPLAY_REPLACE_END__*/\n        };\n        \n        dotNodes = Array.from(dotsNode.querySelectorAll('.embla__dot'));\n        dotNodes.forEach((dotNode, index) => {\n            dotNode.addEventListener('click', () => scrollTo(index), false);\n        });\n    };\n    \n    const toggleDotBtnsActive = () => {\n        const previous = emblaApi.previousSnap();\n        const selected = emblaApi.selectedSnap();\n        dotNodes[previous].classList.remove('embla__dot--selected');\n        dotNodes[selected].classList.add('embla__dot--selected');\n    };\n    \n    addDotBtnsWithClickHandlers();\n    toggleDotBtnsActive();\n    \n    emblaApi\n        .on('reinit', addDotBtnsWithClickHandlers)\n        .on('reinit', toggleDotBtnsActive)\n        .on('select', toggleDotBtnsActive);\n};\n";